import React, { Component } from "react";
import bgImage from "../Resource/contactBg.jpg";
import linkedIn from "../Resource/linkedIn.png";
import facebook from "../Resource/facebook.png";
import instagram from "../Resource/instagram.png";
import twitter from "../Resource/twetter.png";

class ContactPage extends Component {
  render() {
    return (
      <div
        id="sectionContact"
        style={{
          backgroundImage: `URL(${bgImage})`,
          backgroundSize: "100%",
          paddingBottom: "1%",
          minHeight: "700px",
        }}
      >
        <div className="container">
          <h1>Contacts</h1>
          <div
            className="container"
            style={{
              paddingTop: "20px",
              paddingBottom: "20px",
              backgroundColor: "#060a0c8c",
              marginTop: "5%",
              marginBottom: "10%",
            }}
          >
            <h4
              style={{
                color: "white",
                textAlign: "center",
              }}
            >
              niteshsinghnrs@gmail.com
            </h4>
            <hr
              style={{
                textAlign: "center",
                margin: "3%",
                backgroundColor: "white",
                padding: "1px",
              }}
            />
            <div className="row" style={{ margin: "8%" }}>
              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href="https://www.linkedin.com/in/nitesh-singh-69167453/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={linkedIn}
                    className="img-fluid contactSocailNetwork"
                    alt="linkedInIcon"
                  />
                </a>
              </div>

              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href="https://www.facebook.com/niteshsinghnrs1"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={facebook}
                    className="img-fluid contactSocailNetwork"
                    alt="facebookIcon"
                  />
                </a>
              </div>
              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href="https://www.instagram.com/niteshsinghnrs/"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={instagram}
                    className="img-fluid contactSocailNetwork"
                    alt="InstaIcon"
                  />
                </a>
              </div>
              <div className="col-md-3 col-sm-3 col-6 socailImage">
                <a
                  href="https://twitter.com/niteshsinghnrs1"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <img
                    src={twitter}
                    className="img-fluid contactSocailNetwork"
                    rel="noopener noreferrer"
                    alt="twitterIcon"
                  />
                </a>
              </div>
            </div>
            <hr
              style={{
                textAlign: "center",
                margin: "3%",
                backgroundColor: "white",
                padding: "1px",
              }}
            />
            <h3
              style={{
                color: "white",
                textAlign: "center",
              }}
            >
              Thank you for Visiting
            </h3>
          </div>
        </div>
      </div>
    );
  }
}

export default ContactPage;
