import * as actionTypes from "./ActionType";

const initialState = {
  imageArray: [],
};

export default function appReducer(state = initialState, action) {
  switch (action.type) {
    case actionTypes.FETCH_GALLERY_IMAGE:
      return {
        ...state,
        imageArray: action.payload,
      };
    default:
      return state;
  }
}
